<template>
    <article class="modal">
        <div class="modal__wrapper">
            <div class="modal__container">
                <div class="modal__content">
                    <div class="modal__col">
                      <div class="modal__named">
                        <h1 class="modal__title">
                          {{ thisCase.type }}
                        </h1>
                        <h3 class="modal__model" :style="{ color: thisCase.modelColor }">
                          {{ thisCase.model }}
                        </h3>
                      </div>
                      <div class="modal__description">
                          <p><b>Тип:</b> {{ thisCase.description.type }}</p>
                          <p><b>Совместимый бренд:</b> {{ thisCase.description.brand }}</p>
                          <p><b>Назначение:</b> {{ thisCase.description.destination }}</p>
                          <p><b>Дизайн:</b> {{ thisCase.description.design }}</p>
                          <!--<p><b>Размер:</b> {{ thisCase.description.size }}</p>-->
                          <p class="selected"><b>Модель:</b>
                            <select name="" id="" @change="choiseOfModel">
                              <option :value="item.model" v-for="item in thisCase.description.size_price" :key="item.id">{{ item.model }}</option>
                            </select>
                          </p>
                          <p><b>Размер:</b> {{ sizeAndPrice.size }}</p>
                          <p><b>Цена:</b>  {{ sizeAndPrice.price }}грн.</p>
                      </div>
                      <div class="modal__btn">
                        <!--<router-link to="/shopping-cart"  tag="button"  :exact="true">-->
                          <button :style="{ background: thisCase.modelColor }" @click.prevent="addToShoppingCase(thisCase.id, sizeAndPrice.model)">
                            Заказать
                          </button>
                      </div>
                    </div>
                    <div class="modal__col">
                      <div class="modal__img" v-if="thisCase.imgs.length < 2">
                          <img :src="thisCase.imgs[0]" alt="">
                      </div>
                      <swiper :options="swiperOption" ref="caseSlider" class="case_details" v-else>
                        <!-- slides -->
                        <swiper-slide v-for="item in thisCase.imgs" :key="item.id">
                          <div class="swiper__wrapper">
                            <div class="swiper__img">
                              <img :src="item" alt="">
                            </div>
                          </div>
                        </swiper-slide>
                        <!-- Optional controls -->
                        <div class="swiper-pagination pagination"  slot="pagination"></div>
                      </swiper>
                    </div>
                </div>
            </div>
            <div class="modal__footer">
                <div class="modal__back">
                  <router-link to="/"  tag="a" :exact="true">
                    <img src="img/goBack.png" alt="">
                  </router-link>
                </div>
            </div>
        </div>
    </article>
</template>

<script>
  import { swiper, swiperSlide } from 'vue-awesome-swiper';
  // import { router } from "../../routes/router";
  import store from '../../store.js';

  export default {
    name: 'modal',
    // props: ['DataRow'],
    components: {
      swiper,
      swiperSlide,
    },
    data() {
        return {
          sizeAndPrice: {
            size: null,
            price: null,
            model: null,
          },
          deviceWidth: null,
          detailsCase: [
              {
                  id: 'dark_black',
                  type: 'apple iphone',
                  model: 'dark black',
                  modelColor: '#000',
                  imgs: [
                      'img/case/black.png',
                      'img/case4.png',
                  ],
                  description: {
                      type: 'Чехол-накладка',
                      brand: 'Apple iPhones',
                      destination: 'Грязеотталкивающий, Противоударный',
                      design: 'Глянец, Бизнес, Прозрачный, Одноцветный',
                      size_price: [
                        {
                          model: 'XS',
                          size: '6.5',
                          price: '325'
                        },
                        {
                          model: 'XR',
                          size: '6.1',
                          price: '275'
                        },
                        {
                          model: 'X',
                          size: '5.8',
                          price: '250'
                        },
                        {
                          model: '8',
                          size: '5.5',
                          price: '225'
                        },
                        {
                          model: '7 plus',
                          size: '6.3',
                          price: '175'
                        },
                        {
                          model: '7',
                          size: '4.8',
                          price: '150'
                        }
                      ]
                  }
              },
              {
                  id: 'deep_brown',
                  type: 'apple iphone',
                  model: 'bright yellow',
                  modelColor: '#d4581b',
                  imgs: [
                      'img/case/yellow.png',
                      'img/case4.png',
                  ],
                  description: {
                      type: 'Чехол-накладка',
                      brand: 'Apple iPhones',
                      destination: 'Грязеотталкивающий, Противоударный',
                      design: 'Глянец, Бизнес, Прозрачный, Одноцветный',
                      size_price: [
                        {
                          model: 'Xr',
                          size: '6.5',
                          price: '325'
                        },
                        {
                          model: 'Xs',
                          size: '6.1',
                          price: '275'
                        },
                        {
                          model: 'X',
                          size: '5.8',
                          price: '250'
                        }
                      ]
                  }
              },
              {
                  id: 'brown',
                  type: 'apple iphone',
                  model: 'deep brown',
                  modelColor: '#8d5139',
                  imgs: [
                      'img/case/brown.png',
                  ],
                  description: {
                      type: 'Чехол-накладка',
                      brand: 'Apple iPhones',
                      destination: 'Грязеотталкивающий, Противоударный',
                      design: 'Глянец, Бизнес, Прозрачный, Одноцветный',
                      size_price: [
                        {
                          model: 'X',
                          size: '6.5',
                          price: '325'
                        },
                        {
                          model: 'Xs',
                          size: '6.1',
                          price: '275'
                        },
                        {
                          model: 'Xr',
                          size: '5.8',
                          price: '250'
                        }
                      ]
                  }
              },
              {
                  id: 'dark',
                  type: 'apple iphone',
                  model: 'dark black',
                  modelColor: '#000',
                  imgs: [
                      'img/case/black.png',
                      'img/case4.png',
                  ],
                  description: {
                      type: 'Чехол-накладка',
                      brand: 'Apple iPhones',
                      destination: 'Грязеотталкивающий, Противоударный',
                      design: 'Глянец, Бизнес, Прозрачный, Одноцветный',
                      size_price: [
                        {
                          model: 'Xs',
                          size: '6.5',
                          price: '325'
                        },
                        {
                          model: 'XR',
                          size: '6.1',
                          price: '275'
                        },
                        {
                          model: 'X',
                          size: '5.8',
                          price: '250'
                        }
                      ]
                  }
              },
              {
                  id: 'deep',
                  img: '../../img/case/yellow.png',
                  type: 'apple iphone',
                  model: 'bright yellow',
                  modelColor: '#d4581b',
                  imgs: [
                      'img/case/yellow.png',
                      'img/case4.png',
                  ],
                  description: {
                      type: 'Чехол-накладка',
                      brand: 'Apple iPhones',
                      destination: 'Грязеотталкивающий, Противоударный',
                      design: 'Глянец, Бизнес, Прозрачный, Одноцветный',
                      size_price: [
                        {
                          model: 'XS',
                          size: '6.5',
                          price: '325'
                        },
                        {
                          model: 'XR',
                          size: '6.1',
                          price: '275'
                        },
                        {
                          model: 'X',
                          size: '5.8',
                          price: '250'
                        }
                      ]
                  }
              },
          ],
          thisCase: null,
          swiperOption: {
            autoHeight: true,
            // loop: true,
            centeredSlides: true,
            spaceBetween: 0,
            pagination: {
              el: '.swiper-pagination',
              clickable: true,
            }
          }
        }
    },
    created() {
      let vm = this;
      this.thisCase = this.lodash.filter( this.detailsCase , function(item) {
          return item.id === vm.$route.params.model;
      })[0];

      this.sizeAndPrice.size = this.thisCase.description.size_price[0].size;
      this.sizeAndPrice.price = this.thisCase.description.size_price[0].price;
      this.sizeAndPrice.model = this.thisCase.description.size_price[0].model;

      this.deviceWidth = (window.innerWidth > 0) ? window.innerWidth : screen.width;

      if( this.deviceWidth > 767 ) {
        this.swiperOption.autoHeight = false;
      } else {
        this.swiperOption.autoHeight = true;
      }
    },
    computed: {
      swiper() {
        if( this.thisCase.imgs.length > 1 )
          return this.$refs.caseSlider.swiper
      }
    },
    mounted() {
      if( this.thisCase == undefined || this.thisCase  == null ) {
        this.$router.push('/*')
      }

      if( this.thisCase.imgs.length > 1 )
        this.swiper.slideTo(0, 1000, false)
    },
    methods: {
      choiseOfModel(e ) {
        console.log( e.target.value )
        for(let key in this.thisCase.description.size_price) {
          if( e.target.value == this.thisCase.description.size_price[key].model ) {
            this.sizeAndPrice.size = this.thisCase.description.size_price[key].size;
            this.sizeAndPrice.price = this.thisCase.description.size_price[key].price;
            this.sizeAndPrice.model = this.thisCase.description.size_price[key].model;
          }
        }

      },
      addToShoppingCase(id, model) {
        store.setItemToStore( id, model );
        this.$router.push('/shopping-cart');
      }
    },
  }
</script>

<style lang="scss">
  .case_details {
    .swiper {
      &-container {
        width: 100%;
        height: auto;
        /*height: 4rem;*/
        /*background-color: red;*/
      }
      &-slide {
        width: 100% !important;
      }
      &__wrapper {
        display: flex;
        justify-content: center;
        align-items: center;
        height: 100%;
      }
      &__img {
        max-width: 100%;
        margin: 10%;
        @media screen and (max-width:1440px) {
          /*margin: 0;*/
          display: flex;
          justify-content: center;
          align-items: center;
        }
        img {
          @media screen and (max-width:1440px) {
            max-width: 80%;
          }
        }
      }
    }
  }

  .pagination {
    .swiper-pagination-bullet {
      border: 2px solid #000;
      background: transparent;
      opacity: 1;
      &-active {
        background-color: #000;
      }
    }
  }

  .selected {
    display: flex;
    align-items: center;
    select {
      margin: 0 1.25rem;

      color: #000000;
      font-family: "Oswald - Regular Italic";
      font-size: 24px;
      font-weight: 400;
      text-transform: uppercase;
      letter-spacing: 0.75px;
    }
  }
</style>



